<template>
    <div>
        <div class="container">
            <div class="row">
                <div class="col s12 m8 offset-m2">
                    <div class="login card-panel  white-text center">
                        <h3 class="">Login</h3>
                        <form>
                            <div class="input-field">
                                <i class="material-icons prefix">email</i>
                                <input type="text" class="white-text" id="email">
                                <label class="white-text" for="email">Email</label>
                            </div>
                            <div class="input-field">
                                <i class="material-icons prefix">lock</i>
                                <input  type="text" class="white-text" id="password" >
                                <label class="white-text" for="password">Password</label>
                            </div>
                            <button class="btn btn-large grey lighten-4 black-text">Login</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'login',
    data: function() {
        return {}
    }
}
</script>
<style scoped>
.login{
    background: #274d8a;
}
 .input-field input[type=text]:focus {
     border-bottom: 1px solid greenyellow;
     box-shadow: 0 1px 0 0 greenyellow;
   }
    .input-field .prefix.active {
     color: greenyellow;
   }
</style>





